import * as Yup from "yup";

export const userSchema = Yup.object({
  weight: Yup.number()
    .max(320, "Zadajte svoju hmotnos v kilogramoch")
    .required("Toto pole je povinn"),
  height: Yup.number()
    .max(235, "Toto pole je povinn")
    .required("Toto pole je povinn"),
  age: Yup.number()
    .max(120, "Toto pole je povinn")
    .required("Toto pole je povinn"),
  sex: Yup.string()
    .min(4, "Toto pole je povinn")
    .required("Toto pole je povinn"),
  stomachState: Yup.string()
    .min(4, "Toto pole je povinn")
    .required("Toto pole je povinn"),
  drinkingHabits: Yup.string()
    .min(4, "Toto pole je povinn")
    .required("Toto pole je povinn"),
});
