import React from "react";
import { Formik } from "formik";
import { RouteComponentProps } from "react-router";
import { useLocalStorage } from "../utils/useLocalState";
import { userParamCalc } from "../utils/userParamCalc";
import { userSchema } from "../validationSchemas/userSchema";
// import { Button } from "@mui/material"
import "../Style.css";
import { FormControlLabel, Radio, RadioGroup } from "@mui/material";

interface UserProps extends RouteComponentProps {}

export const User: React.FC<UserProps> = ({ history }) => {
  const [userParams, setUserParams] = useLocalStorage("userParams", {});

  // const goHome = () => {
  //   history.push("/");
  // };

  return (
    <Formik
      initialValues={{
        weight: userParams.weight || "",
        height: userParams.height || "",
        age: userParams.age || "",
        sex: userParams.sex || "",
        stomachState: userParams.stomachState || "",
        drinkingHabits: userParams.drinkingHabits || "",
      }}
      onSubmit={(values) => {
        const params = userParamCalc(
          values.weight,
          values.height,
          values.age,
          values.sex,
          values.stomachState,
          values.drinkingHabits
        );
        setUserParams({
          weight: values.weight,
          height: values.height,
          age: values.age,
          sex: values.sex,
          stomachState: values.stomachState,
          drinkingHabits: values.drinkingHabits,
        });

        history.push(
          `/user/drinks/${params.widmarkFactor};${params.eliminationRate};${params.absorptionRate};${params.weight}`
        );
      }}
      validationSchema={userSchema}
    >
      {({
        touched,
        errors,
        values,
        handleChange,
        handleBlur,
        handleSubmit,
        setFieldValue,
        isValid,
      }) => (
        <div className="userform__container container">
          {/* <div className="Logo userform__logo-web" onClick={goHome}>
            <h1>Alkoholov analyztor</h1>
          </div> */}
          <form className="userform__form-container">
            {/* <div
              className="Logo userform__logo-mobile"
              onClick={goHome}
              style={{ textAlign: "center" }}
            >
              <h1>Alkoholov analyztor</h1>
            </div> */}
            {/* <div className="userform__title-container userform__g1">
              <h2>Zadajte svoje fyzick charakteristiky</h2>
            </div> */}
            <p
              style={{
                fontSize: "x-large",
                gridColumn: "1 / span 2",
                textAlign: "center",
              }}
            >
              Zadajte svoje fyzick charakteristiky
            </p>

            <div className="userform__g2 userform__radiobuttons-container">
              <RadioGroup
                style={{ display: "contents" }}
                row
                aria-labelledby="demo-radio-buttons-group-label"
                defaultValue="female"
                name="radio-buttons-group"
              >
                <FormControlLabel
                  className="userform__radio-label"
                  name="sex"
                  value="male"
                  id="male"
                  control={<Radio style={{ color: "#243E36" }} />}
                  label="Mu"
                  checked={values.sex === "male"}
                  onChange={() => setFieldValue("sex", "male")}
                  style={{
                    marginLeft: 0,
                    marginRight: 0,
                    border:
                      values.sex === "male"
                        ? "1px solid #7CA982"
                        : "1px solid #B1B4B6",
                  }}
                />
                <FormControlLabel
                  className="userform__radio-label"
                  name="sex"
                  value="female"
                  id="female"
                  control={<Radio style={{ color: "#243E36" }} />}
                  label="ena"
                  checked={values.sex === "female"}
                  onChange={() => setFieldValue("sex", "female")}
                  style={{
                    marginLeft: 0,
                    marginRight: 0,
                    border:
                      values.sex === "female"
                        ? "1px solid #7CA982"
                        : "1px solid #B1B4B6",
                  }}
                />
              </RadioGroup>
              {/* <input
                type="radio"
                name="sex"
                value="male"
                id="male"
                checked={values.sex === "male"}
                onChange={() => setFieldValue("sex", "male")}
                style={{ display: "none" }}
              />
              <label
                className="userform__radio-label"
                htmlFor="male"
                style={{
                  border:
                    values.sex === "male"
                      ? "1px solid #7CA982"
                      : "1px solid #B1B4B6",
                }}
              >
                Mu
              </label>
              <input
                type="radio"
                name="sex"
                value="female"
                id="female"
                checked={values.sex === "female"}
                onChange={() => setFieldValue("sex", "female")}
                style={{ display: "none" }}
              />
              <label
                className="userform__radio-label"
                htmlFor="female"
                style={{
                  border:
                    values.sex === "female"
                      ? "0.5px solid #7CA982"
                      : "0.5px solid #B1B4B6",
                }}
              >
                ena
              </label> */}
            </div>
            <hr className="userform__line userform__line1" />
            <h4
              className=" userform__description userform__g3"
              style={{
                color: errors.weight && touched.weight ? "#ff0033" : "#3f4649",
              }}
            >
              Zadajte svoju hmotnos v kilogramoch
            </h4>

            <input
              name="weight"
              placeholder=" (kg)"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.weight}
              type="text"
              pattern="[0-9]*"
              inputMode="numeric"
              className=" userform__text-input userform__g4"
            />

            <h4
              className=" userform__description userform__g5"
              style={{
                color: errors.height && touched.height ? "#ff0033" : "#3f4649",
              }}
            >
              Zadajte svoju vku v centimetroch
            </h4>
            <input
              name="height"
              placeholder=" (cm)"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.height}
              type="text"
              pattern="[0-9]*"
              inputMode="numeric"
              className=" userform__text-input userform__g6"
            />

            <h4
              className=" userform__description userform__g7"
              style={{
                color: errors.age && touched.age ? "#ff0033" : "#3f4649",
              }}
            >
              Zadajte svoj vek
            </h4>
            <input
              name="age"
              placeholder=" (vek)"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.age}
              type="text"
              pattern="[0-9]*"
              inputMode="numeric"
              className=" userform__text-input userform__g8"
            />
            <hr className="userform__line userform__line2" />
            <h4
              className="userform__description userform__g9"
              style={{
                color:
                  errors.stomachState && touched.stomachState
                    ? "#ff0033"
                    : "#3f4649",
              }}
            >
              Boli ste najeden?
              {window.screen.width < 680 ? null : <br />}
            </h4>
            <select
              name="stomachState"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.stomachState}
              className=" userform__select-input userform__g10"
            >
              <option value="25"></option>
              <option value="full">Najeden</option>
              <option value="mostly-full">Skr najeden</option>
              <option value="average">V priemere</option>
              <option value="mostly-empty">Skr hladn</option>
              <option value="empty">Hladn</option>
            </select>

            <h4
              className=" userform__description userform__g11"
              style={{
                color:
                  errors.drinkingHabits && touched.drinkingHabits
                    ? "#ff0033"
                    : "#3f4649",
              }}
            >
              Ako asto spotrebujete alkohol tdenne?
              {window.screen.width < 680 ? null : <br />}
            </h4>
            <select
              name="drinkingHabits"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.drinkingHabits}
              className=" userform__select-input userform__g12"
            >
              <option value="25"></option>
              <option value="everyday">Kad de</option>
              <option value="often">Niekokokrt tdenne</option>
              <option value="sometimes">Raz tdenne</option>
              <option value="rarely">Menej ako raz tdenne</option>
            </select>
            <hr className="userform__line userform__line3" />
            <button
              onClick={() => handleSubmit()}
              type="submit"
              className="full-element userform__button userform__g13"
              disabled={!isValid || "" in values ? true : false}
            >
              Uloi
            </button>
          </form>
        </div>
      )}
    </Formik>
  );
};
