import * as Yup from "yup";

export const drinkSchema = Yup.object({
  type: Yup.string()
    .required("Toto pole je povinn")
    .min(4, "Toto pole je povinn"),
  volume: Yup.number().required("Toto pole je povinn"),
  unit: Yup.string()
    .required("Toto pole je povinn")
    .min(2, "Toto pole je povinn"),
  timePassed: Yup.number().max(30).required("Toto pole je povinn"),
  abv: Yup.number()
    .max(100, "Vntorn sila npoja v %")
    .required("Toto pole je povinn"),
});
